import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-edit-adddress-customer-ghtk',
  templateUrl: './edit-adddress-customer-ghtk.component.html',
  styleUrls: ['./edit-adddress-customer-ghtk.component.scss']
})
export class EditAdddressCustomerGhtkComponent implements OnInit {

  @Input("visible_edit_address_ghtk") visible_edit_address_ghtk: boolean = false;
  @Output("closeAndGetValueEditAddress") closeAndGetValueEditAddress = new EventEmitter<any>();
  @Input("data_edit_address_ghtk") data_edit_address_ghtk;

  constructor(
    private languageService: LanguageService,
    private vhQuerySales: VhQuerySales,
    private vhComponent: VhComponent
  ) {
  }
  addInformation: FormGroup;
  province: any = {
    ProvinceName: ''
  };
  district: any = {
    DistrictName: '',
  };
  ward: any = {
    WardName: ''
  };
  chooseService: any;
  data_delivery: any;
  ngOnInit() {
    this.initForm();
    this.data_delivery = this.data_edit_address_ghtk.data_delivery;
  }
  initForm() {
    this.addInformation = new FormGroup({
      address: new FormControl(this.data_edit_address_ghtk.infoAddressChose.address, Validators.required),
      province: new FormControl(this.data_edit_address_ghtk.infoAddressChose.province, Validators.required),
      district: new FormControl(this.data_edit_address_ghtk.infoAddressChose.district, Validators.required),
      ward: new FormControl(this.data_edit_address_ghtk.infoAddressChose.ward, Validators.required),
      hamlet: new FormControl(this.data_edit_address_ghtk.infoAddressChose.hamlet, Validators.required)
    })
  }
  exportAddress() {
    let data = {
      _id: this.data_edit_address_ghtk.infoAddressChose._id,
      province: this.addInformation.value.province,
      district: this.addInformation.value.district,
      ward: this.addInformation.value.ward,
      address: this.addInformation.value.address,
      hamlet: this.addInformation.value.hamlet,
      date: new Date().toISOString()
    }
    return data;
  }
  /** 
  * hm ny dng  check xem a ch ca khch hng  c trong database hay cha
  * result : true l a ch  c lu khng cn lu li, false tc l phi lu a ch  li
  * */
  onClose() {
    this.closeAndGetValueEditAddress.emit(false);
  }
  updateAddress() {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      this.vhQuerySales.updateAddress(this.data_edit_address_ghtk.infoAddressChose._id, this.exportAddress()).then((res) => {
        if (res) {
          this.vhComponent.hideLoading(0)
          this.closeAndGetValueEditAddress.emit(this.exportAddress());
        } else {
          this.vhComponent.hideLoading(0);
          this.vhComponent.showToast(2000, this.languageService.translate("An error occurred while udpating address"), "alert-toast")
        }
      })
    })
  }
  deleteAddress() {
    this.vhComponent.alertConfirm("", this.languageService.translate("Do you want to delete address ?"), "", "Ok", this.languageService.translate("Cancel"))
      .then(() => {
        this.vhComponent.showLoading("", "transparent-loading").then(() => {
          this.vhQuerySales.deleteAddress(this.data_edit_address_ghtk.infoAddressChose._id).then((res) => {
            this.vhComponent.hideLoading(0).then(() => {
              if (res) {
                this.vhComponent.showToast(2000, this.languageService.translate("This address has been deleted successfully"), "success-toast")
                this.closeAndGetValueEditAddress.emit(true);
              }
            })
          })
        })
      })
      .catch(() => { })
  }
}
