import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { VhAlgorithm } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { GhtkService } from 'src/app/sales/menu/manage/delivery/services/ghtkService/ghtk.service';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-choose-method-ghtk',
  templateUrl: './choose-method-ghtk.component.html',
  styleUrls: ['./choose-method-ghtk.component.scss']
})
export class ChooseMethodGhtkComponent implements OnInit {
  @Input("chooseMethod") chooseMethod: boolean = false;
  @Input("dataToGetMethod") data;
  @Output("cancelModalMethodGhtk") cancelModalMethodGhtk = new EventEmitter<any>();
  roadService: any = {
    fee: ''
  };
  flyService: any = {
    fee: ''
  };
  setHeight = {
    "height": "400px"
  }
  constructor(
    private languageService: LanguageService,
    private vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private ghtkService: GhtkService
  ) { }

  ngOnInit() {
    this.getData();
  }
  getData() {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      Promise.all([
        this.ghtkService.calculateFeeGhtk(this.data, 'road'),
        this.ghtkService.calculateFeeGhtk(this.data, 'fly')
      ]).then((res: any) => {
        this.vhComponent.hideLoading(0).then(() => {
          if (res[0].success && res[0].fee.delivery) {
            this.roadService = {
              name_service: this.languageService.translate("Standard"),
              type: 1, //chun
              ...res[0].fee
            }
          }
          else {
            this.vhComponent.showToast(2000, this.languageService.translate('GHTK does not support delivery at this address, please check again'), "alert-toast");
          }
          if (res[1].success && res[1].fee.delivery) {
            if (this.roadService.fee != res[1].fee.fee) {
              this.flyService = {
                name_service: this.languageService.translate("Fast"),
                type: 2,
                ...res[1].fee
              }
            }
          }
        })
      }).catch(err => { console.error(err); this.vhComponent.hideLoading(0) });
    })
  }
  goBack() {
    this.cancelModalMethodGhtk.emit(false);
  }
  chooseService(item) {
    this.cancelModalMethodGhtk.emit(item);
  }

}
