import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuery, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { GhtkService } from 'src/app/sales/menu/manage/delivery/services/ghtkService/ghtk.service';
import { LanguageService } from 'src/app/sales/services/language.service';
@Component({
  selector: 'app-delivery-ghtk',
  templateUrl: './delivery-ghtk.component.html',
  styleUrls: ['./delivery-ghtk.component.scss']
})
export class DeliveryGhtkComponent implements OnInit {
  bills: Array<any> = [];
  show_bills: any = [];
  //Hm nay 0h- 23h59
  startDateTime = new Date();
  subStartDateTime = new Date();
  endDateTime = new Date();
  subEndDateTime = new Date();
  //bin c show
  status_open: boolean = false;
  data_delivery: any;
  valueOfStatus: number = 1;
  search: string = '';
  list_status: Array<any> = [
    {
      group: 1,
      name: "All"
    },
    {
      group: 0,
      name: "Not enough information"
    },
    {
      group: 2,
      name: "Ready to pick"
    },
    {
      group: 3,
      name: "Delivering"
    },
    {
      group: 4,
      name: "delivered"
    },
    {
      group: 5,
      name: "Returning"
    },
    {
      group: 6,
      name: "Returned"
    },
    {
      group: 7,
      name: "Cancelled"
    },
    {
      group: 8,
      name: "Can not get the goods"
    }
  ]
  disabledDate = (current: Date): boolean => {
    if (current.getTime() < new Date(this.vhAuth.getStore().registrationdate).getTime() - 24 * 60 * 60 * 1000) return true
    return false
  }
  constructor(
    public lang: LanguageService,
    private router: Router,
    private vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private ghtkService: GhtkService,
    private vhQuerySales: VhQuerySales,
    private vhQuery: VhQuery,
    private cdRef: ChangeDetectorRef,
    private vhAuth: VhAuth
  ) {
    this.startDateTime.setHours(0, 0, 0);
    this.subStartDateTime = this.startDateTime;
    this.endDateTime.setHours(23, 59, 59);
    this.subEndDateTime = this.endDateTime;
    this.getSearchAndDate()
  }

  ngOnInit(): void {
    this.getData(this.startDateTime, this.endDateTime);
  }
  goBack() {
    this.router.navigate(["/sales/dashboard/manage/delivery/shipping-bill"]);
  }
  getSearchAndDate() {
    if (this.router.getCurrentNavigation().extras.state) {
      let dataSearchAndDate = this.router.getCurrentNavigation().extras.state.dataSearchAndDate;
      this.search = dataSearchAndDate.search ? dataSearchAndDate.search : "";
      this.startDateTime = dataSearchAndDate.startDateTime;
      this.endDateTime = dataSearchAndDate.endDateTime;
      this.subStartDateTime = this.startDateTime;
      this.subEndDateTime = this.endDateTime;
    }
  }
  /**
   * Hm ly bill trong khong thi gian c set
   */
  getData(startTime, endTime) {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      Promise.all([
        this.vhQuerySales.getlocalDeliverys(),
        this.vhQuerySales.getBills_byFields( { date: { $gte: startTime, $lte: endTime }, bill_type: { $eq: 1 }, id_branch: { $eq: this.vhQuerySales.getDefaultBranch()._id } }, {}), //cha bill_type
      ]).then((res: any) => {
        if (res[0]) this.data_delivery = res[0].filter(item => item.active == true && item.type == 2)[0];
        if (res[1]) {
          let bills = res[1].filter(item => item.id_delivery == this.data_delivery._id);
          this.bills = bills.reverse();
          let process: Array<any> = []
          this.bills.forEach(item => {
            item.name_customer = this.vhQuerySales.getlocalCustomer(item.id_customer) ? this.vhQuerySales.getlocalCustomer(item.id_customer).name : 'Not found'
            if (item.order_code) process.push(this.ghtkService.getStatusBill(this.data_delivery.token, item.order_code))
          })
          Promise.all(process).then((result: Array<any>) => {
            result.forEach(item => {
              let index = this.bills.findIndex(bill => bill.order_code == item.order.label_id)
              this.bills[index].data = item.order
              let data = this.filterGroup(this.bills[index].data?.status);
              this.bills[index].name_group = data.name;
              this.bills[index].group = data.group
            })
            this.status_open = true;
            this.show_bills = [...this.bills];
            this.vhComponent.hideLoading(0)
          }).catch(err => {
            this.vhComponent.hideLoading(0)
            this.vhComponent.showToast(2000, this.lang.translate("An error occurred while getting data"), "alert-toast")
          })
        }
      })
    })
  }
  filterGroup(status) {
    let data: any = {
      group: '',
      name: ''
    };
    switch (status) {
      case 1: case 2: case 8: case 12: case 128:
        data.name = 'Ready to pick';
        data.group = 2;
        break;
      case 3: case 4: case 9: case 10: case 49: case 123: case 401:
        data.name = 'Delivering';
        data.group = 3
        break;
      case 5: case 6: case 45:
        data.group = 4,
          data.name = 'delivered'
        break;
      case 20:
        data.group = 5,
          data.name = 'Returning'
        break;
      case 11: case 21:
        data.group = 6,
          data.name = 'Returned'
        break;
      case -1:
        data.group = 7;
        data.name = 'Canceled';
        break;
      case 7: case 127:
        data.group = 8;
        data.name = "Can not get the goods";
        break;
      default:
        data.group = 1,
          data.name = 'Not enough information'
    }
    return data;
  }
  setStartTime(event) {
    if (this.endDateTime.getTime() - this.startDateTime.getTime() > 2678400000) {
      this.vhComponent.showToast(2000, this.lang.translate("The time to view no more than 30 days"), "alert-toast")
      this.endDateTime = new Date(this.startDateTime.getTime() + 2678313600)
      this.subStartDateTime = this.startDateTime
      this.subEndDateTime = this.endDateTime
      this.getData(this.startDateTime, this.endDateTime);
    }
    else if (this.endDateTime.getTime() - this.startDateTime.getTime() < 0) this.vhComponent.alertMessage("", this.lang.translate("Staring date must be less than or the same as ending date"), "", "OK");
    else this.getData(this.startDateTime, this.endDateTime);
  }
  setEndTime(event) {
    if (this.endDateTime.getTime() - this.startDateTime.getTime() > 2678400000) {
      this.vhComponent.showToast(2000, this.lang.translate("The time to view no more than 30 days"), "alert-toast")
      this.startDateTime = new Date(this.endDateTime.getTime() - 2678313600)
      this.subStartDateTime = this.startDateTime
      this.subEndDateTime = this.endDateTime
      this.getData(this.startDateTime, this.endDateTime);
    }
    else if (this.endDateTime.getTime() - this.startDateTime.getTime() < 0) this.vhComponent.alertMessage("", this.lang.translate("Staring date must be less than or the same as ending date"), "", "OK");
    else this.getData(this.startDateTime, this.endDateTime);
  }
  //searchbill
  searchBill(value) {
    this.show_bills = this.vhAlgorithm.searchList(
      this.vhAlgorithm.changeAlias(value).toLowerCase(),
      this.bills,
      ["bill_code", "order_code"]
    );
  }
  gotoDetail(bill) {
    if (bill.id_customer != "id_retail") {
      this.router.navigate(["/sales/dashboard/manage/delivery/shipping-bill/delivery-ghtk/order-bill-customer-ghtk"], {
        state: {
          billInfo: bill,
          data_delivery: this.data_delivery,
          dataSearchAndDate: {
            search: this.search,
            startDateTime: this.startDateTime,
            endDateTime: this.endDateTime
          }
        }
      });
    }
    else {
      this.router.navigate(["/sales/dashboard/manage/delivery/shipping-bill/delivery-ghtk/order-bill-retail-ghtk"], {
        state: {
          billInfo: bill,
          data_delivery: this.data_delivery,
          dataSearchAndDate: {
            search: this.search,
            startDateTime: this.startDateTime,
            endDateTime: this.endDateTime
          }
        }
      });
    }
  }
  gotoBillDetail(bill) {
    this.router.navigate(["/sales/dashboard/manage/delivery/shipping-bill/delivery-ghtk/detail-bill"],
      {
        state: {
          billInfo: bill,
          data_delivery: this.data_delivery,
          dataSearchAndDate: {
            search: this.search,
            startDateTime: this.startDateTime,
            endDateTime: this.endDateTime
          }
        }
      })
  }
  /**
  * filter cc trng thi ca bill 
  * */
  filterStatus(value) {
    value = parseInt(value);
    if (value == 0) {
      this.show_bills = this.bills.filter(item => !item.data);
    }
    else if (value == 1) {
      this.show_bills = this.bills;
    }
    else if (value == 2) {
      this.show_bills = this.bills.filter(item => item.group == 2)
    }
    else if (value == 3) {
      this.show_bills = this.bills.filter(item => item.group == 3)
    }
    else if (value == 4) {
      this.show_bills = this.bills.filter(item => item.group == 4)
    }
    else if (value == 5) {
      this.show_bills = this.bills.filter(item => item.group == 5)
    }
    else if (value == 6) {
      this.show_bills = this.bills.filter(item => item.group == 6)
    }
    else if (value == 7) {
      this.show_bills = this.bills.filter(item => item.group == 7)
    }
    else if (value == 8) {
      this.show_bills = this.bills.filter(item => item.group == 8)
    }
  }
  onCurrentPageDataChange(event) { }
  heightScroll: string;
  ngAfterViewChecked() {
    if (document.getElementById("table-delivery-ghtk") && document.querySelector(".delivery-ghtk-header") && document.querySelector(".ant-table-pagination")) {
      this.heightScroll = document.getElementById("table-delivery-ghtk").clientHeight - document.querySelector(".delivery-ghtk-header").clientHeight -
        document.querySelector(".ant-table-pagination").clientHeight - 150 + "px";
    }
    this.cdRef.detectChanges();

  }
}
