import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-add-adddress-customer-ghtk',
  templateUrl: './add-adddress-customer-ghtk.component.html',
  styleUrls: ['./add-adddress-customer-ghtk.component.scss']
})
export class AddAdddressCustomerGhtkComponent implements OnInit {

  @Input("visible_add_address_ghtk") visible_add_address_ghtk: boolean = false;
  @Output("closeAndGetValueAddAddress") closeAndGetValueAddAddress = new EventEmitter<any>();
  @Input("data_add_address_ghtk") data_add_address_ghtk;

  constructor(
    private languageService: LanguageService,
    private vhQuerySales: VhQuerySales,
    private vhComponent: VhComponent,
  ) {
  }
  addInformation: FormGroup;
  province: any = {
    ProvinceName: ''
  };
  district: any = {
    DistrictName: '',
  };
  ward: any = {
    WardName: ''
  };
  chooseService: any;
  data_delivery: any;
  ngOnInit() {
    this.initForm();
    this.data_delivery = this.data_add_address_ghtk.data_delivery;
  }
  initForm() {
    this.addInformation = new FormGroup({
      address: new FormControl('', Validators.required),
      province: new FormControl('', Validators.required),
      district: new FormControl('', Validators.required),
      ward: new FormControl('', Validators.required),
      hamlet: new FormControl('', Validators.required)
    })
  }
  createAddress() {
    this.vhComponent.alertConfirm("", this.languageService.translate("Do you want to create address Giao Hang Tiet Kiem ?"), "", "Ok", this.languageService.translate("Cancel"))
      .then(() => {
        let checkAddress: any = this.checkAddress(this.addInformation.value);
        if (checkAddress.result) {
          this.vhComponent.showToast(2000, this.languageService.translate("This address already exists"), "alert-toast")
        } else {
          this.vhComponent.showLoading("", "transparent-loading").then(() => {
            this.vhQuerySales.addAddress(this.exportAddress()).then(res => {
              this.vhComponent.hideLoading(0).then(() => {
                if (res) {
                  this.closeAndGetValueAddAddress.emit(res);
                }
              })
            })
          })
        }
      })
      .catch(() => { })
  }
  exportAddress() {
    let data = {
      id_customer: this.data_add_address_ghtk.billInfo.id_customer,
      type: 2,
      province: this.addInformation.value.province,
      district: this.addInformation.value.district,
      ward: this.addInformation.value.ward,
      address: this.addInformation.value.address,
      hamlet: this.addInformation.value.hamlet,
      date: new Date().toISOString(),
    }
    return data;
  }
  /** 
  * hm ny dng  check xem a ch ca khch hng  c trong database hay cha
  * result : true l a ch  c lu khng cn lu li, false tc l phi lu a ch  li
  * */
  checkAddress(data) {
    let result: boolean = false;
    let id_address;
    //duyt for trng hp trng a ch th return true  khi lu li
    for (let i in this.data_add_address_ghtk.infoAddress) {
      if (this.data_add_address_ghtk.infoAddress[i].address == data.address && this.data_add_address_ghtk.infoAddress[i].ward == data.ward && this.data_add_address_ghtk.infoAddress[i].hamlet == data.hamlet) {
        result = true;
        id_address = this.data_add_address_ghtk.infoAddress[i]._id
      }
    }
    return {
      result: result,
      id_address: id_address
    };
  }
  onClose() {
    this.closeAndGetValueAddAddress.emit(false);
  }


}
