import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DeliveryGhtkComponent } from './delivery-ghtk.component';
import { CreateBillRetailGhtkComponent } from './create-bill-retail-ghtk/create-bill-retail-ghtk.component';
import { CreateBillCustomerGhtkComponent } from './create-bill-customer-ghtk/create-bill-customer-ghtk.component';
import { DetailBillGhtkComponent } from './detail-bill-ghtk/detail-bill-ghtk.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { ChooseMethodGhtkComponent } from './components/choose-method-ghtk/choose-method-ghtk.component';
import { ListAdddressCustomerGhtkComponent } from './components/list-adddress-customer-ghtk/list-adddress-customer-ghtk.component';
import { AddAdddressCustomerGhtkComponent } from './components/add-adddress-customer-ghtk/add-adddress-customer-ghtk.component';
import { EditAdddressCustomerGhtkComponent } from './components/edit-adddress-customer-ghtk/edit-adddress-customer-ghtk.component';

const routes: Routes = [
  {
    path: "",
    children: [
      {
        path: "",
        component: DeliveryGhtkComponent
      },
      {
        path: "order-bill-retail-ghtk",
        component: CreateBillRetailGhtkComponent
      },
      {
        path: "order-bill-customer-ghtk",
        component: CreateBillCustomerGhtkComponent
      },
      {
        path: "detail-bill",
        component: DetailBillGhtkComponent
      }
    ]
  }

]

@NgModule({
  declarations: [DeliveryGhtkComponent, CreateBillRetailGhtkComponent, CreateBillCustomerGhtkComponent, DetailBillGhtkComponent, ChooseMethodGhtkComponent, ListAdddressCustomerGhtkComponent, AddAdddressCustomerGhtkComponent, EditAdddressCustomerGhtkComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    FormsModule,
    TranslateModule,
    ReactiveFormsModule
  ]
})
export class DeliveryGhtkModule { }
